import React, { Component } from 'react';
import '../css/Skills.css';
import SkillsTypeComp from '../../type/Type';
import SkillsTypes from '../../utils/SkillsTypes';

export default class Skills extends Component {
    render() {
        return(
            <div className='skills'>
                <div className='skills-text'>Skills</div>
                <div className='skills-all-skills'>
                    {
                        SkillsTypes.map((type) => (
                            <SkillsTypeComp skills={type} />
                        ))
                    }
                </div>
            </div>
        )
    }
}