import React, { Component } from 'react';
import '../css/Projects.css';
import Project from '../../project/Project';
import Kodflix from '../../img/kodflix1.png';
import HouseMarketImg from '../../img/house-market.png';

export default class Projects extends Component {
    render() {
        return(
            <div className='projects'>
                <Project  image={Kodflix} title={'Kodflix'} />
                <Project  image={HouseMarketImg} title={'House Market'} />
                <Project  image={Kodflix} title={'Kodflix'} />
                <Project  image={Kodflix} title={'Kodflix'} />
            </div>
        );
    }
}