import React from 'react';

const skillsType = (props) => (
    <div className='skills-type'>
        <div className='skills-type-title'>
            {props.skills.title}
        </div>
        <div className='sub-skills'>
            <div className='sub-skills-bullet'>
                <div className='sub-skills-bullet-shape'></div>
            </div>
            {
                props.skills.subSkills.map((skill) => (
                    <div className='sub-skill' key={'skills page, sub skill - ' + skill.name}>
                        <div className='sub-skill-image-wrap'>
                            <div className='sub-skill-image'>
                                <img src={skill.image} alt={skill.name} />
                            </div>
                        </div>
                        <div className='sub-skill-name'>{skill.name}</div>
                    </div>
                ))
            }
        </div>
    </div>
);

export default skillsType;