import React from 'react';
import '../css/Arrow.css';
import handleClick, { bottomOfPage } from '../utils/HandleClick';

const arrow = (props) => (
    <div className='arrow-wrapper' onClick={(e) => handleClick(e)}>
        <div className='arrow'>
            <div className='shape'></div>
        </div>
    </div>
);



export default arrow;