export default function handleClick(e) {
    if(bottomOfPage()) {
        let home = document.querySelector('.home');
        home.scrollIntoView();
        modifyArrow(home);
    }
    else {
        let nextDiv = moveTo();
        nextDiv.scrollIntoView();
        modifyArrow(nextDiv);
    }

}

function moveTo() {
    return getPages().find((page) => {
        return page.offsetTop > window.pageYOffset;
    });
}

function getPages() {
    let allpages = document.querySelector('.all-pages');
    return Array.from(allpages.children).filter((div) => {
        return !div.classList.contains('arrow-wrapper');
    })
}

function modifyArrow(nextDiv) {
    let arrow = document.querySelector('.arrow');
    if(nextDiv === getPages().pop()) {
        if(!arrow.classList.contains('arrow-bottom-of-page')) {
            arrow.classList.add('arrow-bottom-of-page');
        }
    }
    else {
        if(arrow.classList.contains('arrow-bottom-of-page')) {
            arrow.classList.remove('arrow-bottom-of-page');
        }
    }
}

export function bottomOfPage() {
    return Math.ceil(window.innerHeight) + Math.ceil(window.scrollY) >= document.body.offsetHeight;
}